import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import { toast } from "react-toastify";

import TeachingPlanAPI from "api/common/teaching-plan";
import DetailGroupAPI from "api/common/detail-group";
import subMajorApi from "api/common/subMajor";


export default function TableFilter({
  currentFilter,
  handleKHGDFilterOnChange,
  handleCTGNFilterOnChange,
  handleFilterOnClick,
  setPageable
}) {
  const { t } = useTranslation();

  const [khgd, setKHGD] = useState([]);
  const [ctgn, setCTGN] = useState([]);

  const [subMajorId, setSubMajorId] = useState(null);
  const [listSubMajors, setListSubMajors] = useState([]);

  const getListSubMajors = async () => {
    try {
      const listSubMajorRes = await subMajorApi.getAll();
      if (listSubMajorRes) {
        setListSubMajors(listSubMajorRes.contents);
      }
    } catch (err) {
      setListSubMajors([])
    }
  }
  useEffect(() => {
    (async () => {
      await getListSubMajors();
    })()
  }, []);

  const handleChooseSubMajor = (e) => {
    setSubMajorId(e?.target?.value);
    setPageable(old => {
      return {...old, filter: {
        ...old.filter,
        idKHGD: null,
      }}
    })
  }

  useEffect(async () => {
    try {
      let query = {};
      if(subMajorId){
        query = {CTNganhDaoTao: subMajorId}
      }
      const khgdRecord = await TeachingPlanAPI.getAll(query);
      setKHGD(khgdRecord.contents);

      // const ctgnRecord = await DetailGroupAPI.getAll();
      // setCTGN(ctgnRecord.contents);
    } catch (err) {// eslint-disable-next-line
      console.log(`err`, err)
    }
  }, [subMajorId]);

  return (
    <Form className="d-flex flex-wrap">
      <FormGroup style={{width:"fit-content"}}>
        <Label >{t("admin.knowledge_list.SUB_MAJOR")}</Label>
        <Input
          type="select"
          value={subMajorId}
          onChange={handleChooseSubMajor}
        >
          <option value={-1}>{t("")}</option>
          {listSubMajors &&
            listSubMajors.map((item, index) => {
              return (
                <option key={index + 1} value={item.id}>
                  {item.khoa} -{" "} {` ${item.nganhDaoTao?.ten} - ${item?.nganhDaoTao?.chuongTrinhDaoTao?.maCTDT}`}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;

      {subMajorId ? <FormGroup>
        <Label for="idKHGD">{t("admin.detail_teaching_plan.SEMESTER_NAME")}</Label>
        <Input
          type="select"
          value={currentFilter.idKHGD !== null ? currentFilter.idKHGD : -1}
          onChange={handleKHGDFilterOnChange}
        >
          <option value={-1}>{t(" ")}</option>
          {khgd &&
            khgd.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.tenHocKy}
                </option>
              );
            })}
        </Input>
      </FormGroup> : ""}
      &emsp;
      {/* <FormGroup>
        <Label for="idCTGN">{t("admin.detail-plan.DETAIL_GROUP")}</Label>
        <Input
          type="select"
          value={currentFilter.idCTGN !== null ? currentFilter.idCTGN : -1}
          onChange={handleCTGNFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {ctgn &&
            ctgn.map((item, index) => {
    console.log("🚀 ~ file: TableFilter.js ~ line 113 ~ useEffect ~ err", err)
    console.log("🚀 ~ file: TableFilter.js ~ line 113 ~ useEffect ~ err", err)
              return (
                <option key={index} value={item.id}>
                  {item.id}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp; */}

      {currentFilter.idKHGD !== null ? <FormGroup className="align-self-end">
        <Button onClick={handleFilterOnClick}>
          <FaFilter /> {t("admin.common.FILTER")}
        </Button>
      </FormGroup> : ''}
    </Form>
  );
}
